.faq {
    background: url("../../images/faq/bg.jpg") center / cover no-repeat;
    &-items {
        display: flex;
        flex-direction: column;
    }

    .accordion-item {
        opacity: 0;
        visibility: hidden;
        border-radius: 10px;
        background: #ffffff;
        padding: 20px 20px;
        box-shadow: 0px 16px 20px rgba(0, 0, 0, 0.1);
        margin-top: 30px;
        z-index: 2;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        max-height: 710px;
        background: rgba(196, 196, 196, 0.25);
        //box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
        backdrop-filter: blur(4px);
        -webkit-backdrop-filter: blur(4px);
        border-radius: 10px;
        z-index: 2;
      
        .faq__title {
          display: flex;
          margin: 0;
          padding: 0;
          justify-content: space-between;
          align-items: center;
          line-height: 1;
          cursor: pointer;
        }
      
        .faq__title > .arrow {
          color: $black;
        }
      
        .content {
          display: none;
          overflow: hidden;
        }
      
        .content > .entry {
          line-height: 1.74;
      
          p {
            margin-top: 10px;
            font-size: 18px;
            &:last-of-type,
            &:last-child {
              margin-bottom: 0;
            }
          }
        }
      }
}
