.includes {
  &-types {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 51px;
  }

  &-type {
    font-family: "Gilroy", sans-serif;
    font-size: 18px;
    font-weight: 700;
    border-radius: 10px;
    width: 100%;
    line-height: 3;
    background: $blue;
    color: white;
    margin-bottom: 16px;
    padding-left: 10px;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    cursor: pointer;

    &__services {
      display: none;

      &--active {
        display: flex;
        flex-direction: column;
        font-family: 'Gilroy', sans-serif;
        font-weight: 400;
        font-size: 16px;
      }

      &-item {
        transition: 0.5s all;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        padding: 5px 20px 5px 20px;
        background-color: white;

        &:first-child {
          margin-top: 25px;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
        }

        &:last-child {
          margin-bottom: 23px;
          border-bottom-left-radius: 10px;
          border-bottom-right-radius: 10px;
        }

        &--bg {
          background-color: #DBE2EF;
        }
      }
    }
  }
}
